form {
    margin-bottom: 25px;
}

.form_wrapper {
    max-width: 100%;
    width: 496px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    background: #FFFFFF;
    border: 0.861171px solid rgba(20, 40, 80, 0.1);
    box-shadow: 0px 6.88937px 10.3341px rgba(20, 40, 80, 0.2);
    border-radius: 5px;
    padding: 20px 43px 40px;

    p {
        font-style: normal;
        font-weight: 900;
        font-size: 24px;
        line-height: 33px;
        color: #009688;
        margin-bottom: 25px;
    }

    .form_input_wrapper {
        width: 100%;
        margin: 0 0 24px 0;

        input {
            background: #F3F3F4;
            border-radius: 5px;
            width: 100%;
            padding: 13px 20px;
        }

        input:focus {
            outline: none;
        }
    }

    .login-btn {
        width: 100%;

        button {
            display: flex;
            justify-content: center;
            background: #009688;
            border: 1px solid #009688;
            border-radius: 5px;
            width: 100%;
            color: #FFFFFF;
            padding: 13px 20px;
            cursor: pointer;
            transition: all 0.3s ease;

            &:hover {
                background: #016359;
            }
        }
    }
}